import React, { useState, forwardRef, useEffect } from "react";
import styled from 'styled-components';
import color from '../color-css/color'
import usePlacesAutocomplete, { getGeocode, getZipCode } from "use-places-autocomplete";
import ValidationIndicator from "../ValidationIndicator";
import img from '../../assets/images/powered_by_google_on_white.png'
import useOnclickOutside from "react-cool-onclickoutside"
import Label from '../Label'
import LabelIcon from '../Label/LabelIcon'
import List from './List'

export type Props = {
  onSelectedItemProp?: (wasSelected: boolean) => void,
  inputFromProps?: string,
  isValidated?: boolean,
  handleAddressInfo?: (cityValue: string, state: string, postcode: string) => void,
  label: string,
  placeholder: string
  hasAddressValueChange?: (wasSelected: boolean) => void,
}

interface InputField {
  onInputSucessAsBackgroundColor: boolean
  onInputSucessListener?: (isSucess: boolean) => boolean,
}

const InputField = styled.input<InputField>`
  font-family: museo-sans, sans-serif;
  @media screen and (max-width: 768px) {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding-left: 0.9375rem;
  }
  -webkit-appearance: none;
  width: 34rem;
  height: 1.1875rem;
  background-color: ${props => props.onInputSucessAsBackgroundColor === true ? color.blue : color.grey};
  transition: background 0.5s ease-in-out;
  border: 1px solid ${color.white};
  outline: none;
  color: ${props => props.onInputSucessAsBackgroundColor === true ? color.blue_17_percent : color.grey_dark};
  font-size: 1rem;
  padding: 0.75rem 5.375rem 0.6875rem 1.5625rem;
  border-radius: 0.625rem;  
  ::placeholder,
  ::-webkit-input-placeholder {
    color: ${color.grey_darker};
    font-family: museo-sans, sans-serif;
    font-size: 1rem;
  }
  &:focus {
    ::-webkit-input-placeholder { color:transparent; }
    ::-moz-placeholder { color:transparent; }
    border: 1px solid ${color.blue_darker};
  }
  margin-left: -0.125rem;
`

const HoveringWrapper = styled.div`
  margin-top: 1.25rem;
  &:hover ${InputField} {
    transition: border 0.5s ease-in-out;
    border: 1px solid ${color.blue_darker};
    border-radius: 0.625rem;
    cursor: pointer;	
  }
`

interface DropdownContainer {
  isShow: boolean
}

const DropdownContainer = styled.div<DropdownContainer>`
  display: ${props => props.isShow ? 'block' : 'none'};
  position: relative;
  margin-top: 0.8125rem;
  width: 40.9375rem;
  height: 14.0625rem;
  @media screen and (max-width: 768px) {
    width: 100%;
    box-sizing: border-box;
    height: 12.875rem;
  }
  background: ${color.white} 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 50px #012F571A;
  border-radius: 10px;
  &:before {
    content: "";
    position: absolute;
    left: 48%;
    top: -2rem;
    width: 0;
    height: 0;
    border-top: 1.375rem solid transparent;
    border-right: 0.75rem solid transparent;
    border-bottom: 0.8125rem solid ${color.white};
    border-left: 0.75rem solid transparent;
`

const DropdownFooter = styled.div`
  position:absolute;
  bottom:0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  width: 90%;
  height: 3.9375rem;
  margin: 0 1.75rem;
  border-top: 1px solid ${color.grey_79_percent};
  @media screen and (max-width: 768px) {
    width: 80%;
    height: 2.5rem;
  }
`

const DropdownFooterContent = styled.div`
  font-family: museo-sans, sans-serif;
  font-weight: 500;
  font-style: italic;
  font-size: 1rem;
  color: ${color.grey_darker};
  margin: auto;
  margin-left: 0;
  margin-right: 0;
  line-height: 1.625rem;
  @media screen and (max-width: 768px) {
    font-size: 0.875rem;
    line-height: 1.25rem;
  }
  @media screen and (max-width: 576px) {
    font-size: 0.8125rem;
    line-height: 0.875rem;
  }
`

const DropdownFooterImg = styled.span`
  margin: auto;
  margin-left: 0;
  margin-right: 0;
`

interface DropdownSuggestContent {
  value?: (input: string) => string,
}

const DropdownSuggestContent = styled.span``

const DropdownSuggestContentKey = styled.div`
  margin-bottom: 0.875rem;
  @media screen and (max-width: 768px) {
    margin-bottom: 1.1875rem;
  }
  &:hover ${DropdownSuggestContent} {
    color: ${color.blue_17_percent}
`

interface showSpinner {
  isShow: boolean
}

const Spinner = styled.div<showSpinner>`
  display: ${props => props.isShow ? 'block' : 'none'};
  background-color: transparent;
  border: 5px solid ${color.blue};
  opacity: .9;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-radius: 100px;
  top: 3.4375rem;
  width: 2.5rem;
  height: 2.5rem;
  margin: 0 auto;
  position: relative;
  -moz-animation: spinoffPulse 1s infinite linear;
  -webkit-animation: spinoffPulse 1s infinite linear;
  @-moz-keyframes spinoffPulse {
      0% { -moz-transform:rotate(0deg); }
    100% { -moz-transform:rotate(360deg);  }
  }
  @keyframes spinoffPulse {
      0% { -webkit-transform:rotate(0deg); }
    100% { -webkit-transform:rotate(360deg); }
  }
`

interface Wrapper {
  onInputSucessListener?: (isSucess: boolean) => boolean,
}
const Wrapper = styled.div<Wrapper>`
  display: block;
`

type Ref = HTMLInputElement

const PlacesAutocomplete = forwardRef<Ref, Props>((props: any, aRef) => {
  const { inputFromProps, isValidated, onSelectedItemProp,
    handleAddressInfo, label, placeholder, hasAddressValueChange } = props
  const [inputSuccess, setInputSuccess] = useState(false)
  const [visibility, setVisibility] = useState(false)
  const [showingDropdown, setShowingDropdown] = useState(false)
  const [clickedOutsideState, setClickedOutside] = useState(false)
  const [validatorMsg, setValidatorMsg] = useState('')
  const [labelIconState, setLabelIconStateState] = useState(true)
  const [postcodeArray, setPostcodeArray] = useState<any>([''])
  const [addressArray, setAddressArray] = useState([''])
  const [showingSpinner, setShowingSpinner] = useState(false)
  const [isAddressChosen, setIsAddressChosen] = useState(false)
  const [hasInputChange, setInputChange] = useState(false)

  const {
    value,
    suggestions: { data },
    setValue
  } = usePlacesAutocomplete({
    requestOptions: {
      componentRestrictions: { country: 'au' },
    },
    debounce: 300,
  },
  );

  const handleInput = (e: any) => {
    setValue(e.target.value);
    e.target.value.length >= 2 ? setShowingDropdown(true) : setShowingDropdown(false);
    setIsAddressChosen(false)
    setInputChange(true)
  };

  const handleSelect = (description: string) => () => {
    let streetaddress = description.substr(0, description.indexOf(','));
    let streetState = description.replace(streetaddress, '')
    streetState = streetState.replace(',', '')
    streetState = streetState.substr(1,streetState.length)
    let postcode = streetState.slice(-4)
    streetState = streetState.replace(postcode, '')
    let stateOrTerritory: any = streetState.slice(-4).trim()
    let city: any = streetState.replace(streetState.slice(-4).trim(), '')
    city = city.substr(0, city.length-2)
    handleAddressInfo ? handleAddressInfo(city, stateOrTerritory, postcode) : {}
    setValue(streetaddress)
    setShowingDropdown(false)
    setIsAddressChosen(true)
  };

  function getGeocodeFromAPI(input: string) {
    return new Promise((resolve) => {
      getGeocode({ address: input })
        .then((results) => getZipCode(results[0], false))
        .then((zipCode) => {
          resolve(zipCode)
        })
    })
  }

  function loadGeocode() {
    let allGeocode: any = []
    data.forEach(
      (each) => {
        allGeocode.push(getGeocodeFromAPI(each.description))
      }
    )
    Promise.all(allGeocode).then((geocode) => {
      renderGeocode(geocode)
    })
  }

  function renderGeocode(geocode: any) {
    setPostcodeArray(geocode)
  }

  useEffect(() => {
    loadGeocode()
  }, [data])

  useEffect(() => {
    let array: string[] = []
    for (let i = 0; i < data.length; i++) {
      let test = data[i].description + postcodeArray[i]
      array.push(test)
    }
    setAddressArray(array)
  }, [postcodeArray])

  useEffect(() => {
    if (isAddressChosen !== true) {
      if (addressArray[0] === undefined) {
        setShowingDropdown(false)
        setShowingSpinner(true)
      } else {
        setShowingDropdown(true)
        setShowingSpinner(false)
      }
    }
    else {
      setShowingDropdown(false)
    }
  }, [addressArray, showingSpinner, isAddressChosen])

  const rRegardlessSplitter = '+[(?=,)|(?= )|(?=.)]+'

  const renderSuggestions = addressArray.map((each: string) => {
    each = each.replace(', Australia', ' ')
    let findSuggestionIsAddress = each.substring(0, 1)
    if (!isNaN(Number(findSuggestionIsAddress.toString()))) {
      if (value !== undefined) {
        let valueMapper = value.split(" ")
        const lastValue = valueMapper[valueMapper.length - 1]

        let level2 = valueMapper.map(eachValue => '[' + eachValue + ']' + rRegardlessSplitter)
        level2[level2.length - 1] = lastValue

        let pattern = level2.join('')
        let regex = new RegExp(pattern, "gi")
        let suggestString = each.match(regex)
        let parseSuggestStringToString = JSON.stringify(suggestString)
        parseSuggestStringToString = parseSuggestStringToString.replace('["', "");

        let suggestResult =
          parseSuggestStringToString.substring(0, parseSuggestStringToString.indexOf('"'))

        const suggestHighlight = each.substring(0, suggestResult.length) || undefined
        const suggest = each.substring(suggestResult.length, each.length)

        return suggestHighlight ? (
          <DropdownSuggestContentKey key={each} onClick={handleSelect(each)}>
            <DropdownSuggestContent><strong>{suggestHighlight}</strong>{suggest}</DropdownSuggestContent>
          </DropdownSuggestContentKey>
        ) : undefined
      }
    }
  })

  const onValidate = (e: any) => {
    let inputGetter = e.target.value;
    setInputSuccess(() => !!inputGetter);
    setVisibility(true)
    setInputChange(false)
    onSelectedItemProp ? onSelectedItemProp(true) : {}
  }
  useEffect(() => {
    if (inputFromProps !== undefined && inputFromProps !== '' && isValidated !== false) {
      inputFromProps ? setValue(inputFromProps) : {}
      setVisibility(isValidated)
      setInputSuccess(isValidated);
      onSelectedItemProp ? onSelectedItemProp(isValidated) : {}
    }
    if (inputFromProps === '') {
      setValue(inputFromProps)
      setInputSuccess(false)
    }
  }, [inputFromProps, isValidated])

  useEffect(() => {
    onSelectedItemProp ? onSelectedItemProp(inputSuccess) : {}
    inputSuccess ?
      setValidatorMsg("Thanks") : setValidatorMsg("Enter your address")
  }, [inputSuccess])

  useEffect(() => {
    hasAddressValueChange ? hasAddressValueChange(hasInputChange) : {}
  }, [hasInputChange])

  const handleClickOutsideRef = useOnclickOutside(() => {
    setShowingDropdown(false)
    setClickedOutside(!clickedOutsideState)
  });

  useEffect(() => {
    if (visibility === true && inputSuccess === false) {
      setLabelIconStateState(false)
    } else {
      setLabelIconStateState(true)
    }
  }, [inputSuccess, visibility])

  return (
    <Wrapper>
      <Label>{label} <LabelIcon iconStates={labelIconState}>{'  '}*</LabelIcon></Label>
      <HoveringWrapper>
        <InputField
          name="address"
          placeholder={placeholder}
          value={value}
          onChange={handleInput}
          onBlur={event => onValidate(event)}
          onInputSucessAsBackgroundColor={inputSuccess}
          ref={aRef}
          type="text"
          autoComplete="none"
          autoCorrect="off"
          onInputSucessListener={props.onInputSucessProp ? props.onInputSucessProp(inputSuccess) : {}}
        />
      </HoveringWrapper>
      <DropdownContainer
        isShow={showingDropdown}
        ref={handleClickOutsideRef}>
        <Spinner isShow={showingSpinner} />
        <List children={renderSuggestions} />
        <DropdownFooter>
          <DropdownFooterContent>Cant find your address? Continue typing</DropdownFooterContent>
          <DropdownFooterImg><img src={img} style={{ marginBottom: '0' }} /></DropdownFooterImg>
        </DropdownFooter>
      </DropdownContainer>
      {validatorMsg !== ""
        && <ValidationIndicator isCorrect={inputSuccess} {...{ message: validatorMsg, visibility }} />}
    </Wrapper>
  );
});

export default PlacesAutocomplete