import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import color from '../color-css/color'
import { Spinner } from './Spinner'
import { navigate } from 'gatsby-link'
import submitPromise from '../../utils/submitData'

export enum ButtonStates {
  disabled = 'disabled',
  enabled = 'enabled',
  progressing = 'progressing'
}

export type Props = {
  title: string
  buttonStates: ButtonStates
  onClickCallbackProp: () => void
  onClickExecFunc?: () => any
  onClickNavigateByState?: () => any
  onClickNavigateByPhoneNumber?: () => any
  onSuccessfulCallback?: (data: any) => void
  onFailCallback?: (error: any) => void
  widthSize?: string
}

/**
 * enabled:
 * disabled:
 * progressing:
 */
const handleButtonStates = (getCurrentState: () => ButtonStates) => {
  switch (getCurrentState()) {
    case ButtonStates.enabled:
    default:
      return 'background: #FF6820;'
    case ButtonStates.progressing:
      return 'background: #FF6820;\n'
        + 'cursor: not-allowed;\n'
        + 'pointer-events: none;'
    case ButtonStates.disabled:
      return 'background: #C9C9C9;\n'
        + 'cursor: not-allowed;\n'
        + 'pointer-events: none;'
  }
}

interface ProgressButton {
  onState: () => ButtonStates
  onFocusButtonStates?: ButtonStates
  tabIndex?: any
}
export const StyledProgressButton = styled.button<ProgressButton>`
  outline: none;
  border: none;
  height: 3.125rem;
  width: 100%;
  padding: 0;
  display: block;
  text-align: center;
  line-height: 3.125rem;
  background: ${color.grey} 0% 0% no-repeat padding-box;
  border-radius: 6.25rem;
  opacity: 1;
  font-family: museo-sans, sans-serif;
  font-weight: 700;
  font-size: 1rem;
  letter-spacing: .8px;
  color: ${color.white};
  text-decoration: none;
  ${({ onState }) => handleButtonStates(onState)};
  cursor: pointer;
  &:focus {
    background: #f2560c;
  }
  @media (max-width: 768px) {
    font-size: 0.875rem;
    height: 2.8125rem;
    line-height: 2.8125rem;
  }
`

interface HoverProgressButton {
  onHoverButtonStates?: ButtonStates,
  widthSize?: string
}

const HoverProgressButton = styled.div<HoverProgressButton>`
  width: ${props => props.onHoverButtonStates === ButtonStates.progressing ? '13.875rem' : props.widthSize};
  border-radius: 6.25rem;
  position: relative;
  &:hover ${StyledProgressButton} {
    background: ${props =>
    props.onHoverButtonStates === ButtonStates.enabled || props.onHoverButtonStates === ButtonStates.progressing
      ? '#f2560c' : '#C9C9C9'} ;
  }
  @media (max-width: 768px) {
    width: 10.8125rem;
  }
`

interface ButtonSpinner {
  isShow: boolean
}
const ButtonSpinner = styled(Spinner) <ButtonSpinner>`
  display: ${props => props.isShow === true ? 'block' : 'none'};
  position: absolute;
  right: 0.85rem;
  margin-top: -2.1875rem;
`

const ProcessingTitle = styled.p`
  margin-top: 0.875rem;
  font: italic 1rem museo-sans, sans-serif;
  line-height: 1.75rem;
  letter-spacing: -0.05px;
  color: ${color.grey_29_percent};
  @media (max-width: 768px) {
    font: italic 1rem museo-sans, sans-serif;
  }
`

const ProgressButton: React.FC<Props> = ({
  title,
  buttonStates,
  onClickCallbackProp,
  onClickExecFunc,
  onSuccessfulCallback,
  onFailCallback,
  widthSize,
  onClickNavigateByState,
  onClickNavigateByPhoneNumber
}) => {

  const [buttonState, updateButtonState] = useState(buttonStates)

  useEffect(() => {
    updateButtonState(buttonStates)
  }, [buttonStates, buttonState])

  return (
    <>
      <HoverProgressButton
        onHoverButtonStates={buttonStates}
        widthSize={widthSize}>
        <StyledProgressButton
          tabIndex={buttonState === 'enabled' ? "0" : "-1"}
          onState={() => {
            return buttonState
          }}
          onClick={event => {
            event.preventDefault()
            if(buttonState === 'enabled'){
              onClickCallbackProp()
            }
            new Promise((resolve, reject) => {
              //@ts-ignore
              let test: any = onClickExecFunc()
              if (onClickExecFunc === undefined) {
                reject('Error: There is something wrong')
              }
              else {
                resolve(onClickExecFunc ? onClickExecFunc() : undefined)
              }
            }).then(() => {
              submitPromise().then(result => {
                onSuccessfulCallback ? onSuccessfulCallback(result + ' ' + buttonState) : undefined
                navigate(`/FormSuccessPage/`)
              })
            }).catch(error => {

            })
            // typeof window !== 'undefined' &&
            // submitPromise().then(result => {
            //   onSuccessfulCallback ? onSuccessfulCallback(result + ' ' + buttonState) : undefined
            //   navigate(`/FormSuccessPage/`)
            // }).catch(error => {
            //   console.log(error)
            //   // onFailCallback ? onFailCallback(error) : undefined
            //   // navigate(`/PaymentErrorPage/`)
            // })
            new Promise((resolve, reject) => {
              //@ts-ignore
              let test: any = onClickNavigateByState()
              if (test === '') {
                reject('Error: There is something wrong')
              }
              else {
                resolve(onClickNavigateByState ? onClickNavigateByState() : undefined)
              }
            }).then(() => {
              navigate(`/FormSuccessPage/`)
            }).catch(error => {

            })
            new Promise((resolve, reject) => {
              //@ts-ignore
              let test: any = onClickNavigateByPhoneNumber()
              if (test === 'existing') {
                resolve(onClickNavigateByPhoneNumber ? onClickNavigateByPhoneNumber() : undefined)
              }
            }).then(() => {
              navigate(`/ExistingUserPage/`)
            }).catch(error => {

            })
          }}
        >
          {title}
        </StyledProgressButton>
        {
          buttonStates === 'progressing'
            ? (
              <ButtonSpinner
                isShow={true}
                className="" />
            )
            : (
              <ButtonSpinner
                isShow={false}
                className="" />
            )
        }
      </HoverProgressButton>
      {
        buttonStates === 'progressing' && (
          <ProcessingTitle>
            Don't close your browser
            <span style={{ color: 'orange' }}>*</span>
          </ProcessingTitle>
        )}
    </>
  )
}

export default ProgressButton
