const spaceChar = " "

export const injectSpacetAt = (input: string, pos: number) => {
  let array = input.split('')
  let result = ''
  array.splice(pos, 0, spaceChar);
  result = array.join('')
  return result
}

/**
 * For 02 03 07 08
 * render input as XX XXXX XXXX
 */
export const renderPhoneNumberStrat1 = (input: string) => {
  if (input.startsWith('02') || input.startsWith('03') ||
    input.startsWith('07') || input.startsWith('08')) {
    if (input.length === 3) {
      return injectSpacetAt(input, 2)
    }
    else if (input.length === 8) {
      return injectSpacetAt(input, 7)
    }
    else if (input.length === 10) {
      return input.replace(/^(\d{2})(\d{4})(\d{4})$/g, '$1 $2 $3')
    }
    else if (input.length === 11) {
      const isSpaceAtPos3 = input[2] !== spaceChar
      const isSpaceAtPos7 = input[7] !== spaceChar

      if (isSpaceAtPos3) {
        return injectSpacetAt(input, 2)
      }
      else if (isSpaceAtPos7) {
        return injectSpacetAt(input, 7)
      }
    }

    return input;
  }
  else return input
}

/**
 * For 04
 * render input as XXXX XXX XXX
 * @param input 
 */
export const renderPhoneNumberStrat2 = (input: string) => {
  if (input.startsWith('04')) {
    if (input.length === 5) {
      return injectSpacetAt(input, 4)
    }
    else if (input.length === 9) {

      return injectSpacetAt(input, 8)
    }
    else if (input.length === 10) {
      let test = input.replace(/^(\d{4})(\d{3})(\d{3})$/g, '$1 $2 $3')
      return test
    }
    else if (input.length === 11) {
      const isSpaceAtPos4 = input[4] !== spaceChar
      const isSpaceAtPos8 = input[8] !== spaceChar

      if (isSpaceAtPos4) {
        return injectSpacetAt(input, 4)
      }
      else if (isSpaceAtPos8) {
        return injectSpacetAt(input, 8)
      }
    }
    return input;
  }
}
