import React, { useState, useEffect, useRef } from 'react'
import { useSelector } from 'react-redux'
import { RootState } from 'store'

import styled from 'styled-components'
import { getMessages } from '../dataProcessing'
import Label from '../Label'
import ExpandableParagraph from '../ExpandableParagraph'
import SummarySection from '../SummarySection'
import colors from '../color-css/color'
import TextInput from '../TextInput/index'
import InputFieldContainer from '../Commons/InputFieldContainer'
import ReactRef from 'components/FormHighOrder/types/ReactRef'

import {
  PAY_DEDUCTION_PAYMENT_ELOTRONIC_SIGNATURE,
  PAY_DEDUCTION_PAYMENT_NUMBER,
  PAY_DEDUCTION_PAYMENT_TAB_DATA
} from '../FormHighOrder/types/YourContributionField'
import { PAYROLL_NUMBER_REQUIRED } from '../FormHighOrder/types/YourWorkField'

import color from '../color-css/color'

import ESignature from '../esignature'

export type Props = {
  expandableParagraphIsLongTextProp: boolean
  expandableParagraphDescriptionTextProp: string
  expandableParagraphPayrollDescriptionTextProp?: string
  setPayDeductionTabValidation?: (wasSuccessful: boolean) => void
  onPayDeductionTabData?: (data: any) => void
  inputFromProps?: string
  isPayDeductionDataChange?: (isChange: boolean) => void
}
const Container = styled.div`
  animation-name: flickity;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  @keyframes flickity {
      0% {
          opacity:0;
      }
      100% {
          opacity:1;
      }
  };
  margin-top: 2.5rem;
  max-width: 38.75rem;
  @media (max-width: 768px) {
    max-width: 100%;
  }
`

const SigningText = styled.div
  `
  text-size: 1rem;
  font-family: museo-sans, sans-serif;
  font-weight: 500;
  color: ${color.blue_dark};
  width: 34.0625rem;
  @media (max-width: 768px) {
    width: 100%;
  }
`

const LabelIcon = styled.span`
  color: ${color.blue_darker};
`

const SignButton = styled.button`
  font-size: 1rem;
  line-height: 1.625rem;
  letter-spacing: -0.06px;
  color: ${colors.grey_darker};
  padding: 0.9375rem 0;
  width: 18.875rem;
  border-radius: 10px;
  background: #EFF0F0;
`

interface IsPayrollNumberRequired {
  isRequired: boolean
}

const PayrollNumberRequired = styled.div<IsPayrollNumberRequired>`
  margin-bottom: 2.5rem;
  display: ${props => props.isRequired ? 'block' : 'none'}
`

export const PayDeductionTab: React.FC<Props> = ({
  expandableParagraphIsLongTextProp,
  expandableParagraphDescriptionTextProp,
  expandableParagraphPayrollDescriptionTextProp,
  setPayDeductionTabValidation,
  onPayDeductionTabData,
  inputFromProps, isPayDeductionDataChange
}) => {

  const [electricalSignature, setElectricalSignature] = useState(false)
  const [isPayrollNumberRequired, setIsPayrollNumberRequired] = useState(false)
  const [isValidPayrollNumber, setIsValidPayrollNumber] = useState(false)
  const [hasInputValueChange, setInputValueChange] = useState(false)

  const getPrefetchData = useSelector((state: RootState) =>
    state.FormHighOrder.pageData[3].data[PAY_DEDUCTION_PAYMENT_TAB_DATA]
  )

  const checkFetchData = (nameParam: string) => {
    return getPrefetchData !== undefined && getPrefetchData[nameParam]?.value !== ""
  }

  const Refs: ReactRef = {
    [PAY_DEDUCTION_PAYMENT_NUMBER]: useRef<HTMLInputElement>(null)
  }

  const getPayrollNumberRequire = useSelector((state: RootState) =>
    state.FormHighOrder.pageData[0].data[PAYROLL_NUMBER_REQUIRED].value)

  useEffect(() => {
    if (inputFromProps !== '' && inputFromProps !== undefined) {
      setElectricalSignature(true)
    } else setElectricalSignature(false)
  }, [inputFromProps])

  useEffect(() => {
    if (getPayrollNumberRequire === 'true') {
      setIsPayrollNumberRequired(true)
    } else setIsPayrollNumberRequired(false)
  }, [getPayrollNumberRequire])

  useEffect(() => {
    isPayDeductionDataChange ? isPayDeductionDataChange(hasInputValueChange) : {}
  },[hasInputValueChange])

  useEffect(() => {
    if (isPayrollNumberRequired === true) {
      const isValidated = electricalSignature
        && isValidPayrollNumber

      setPayDeductionTabValidation ? setPayDeductionTabValidation(isValidated) : {}
    }
    else {
      setPayDeductionTabValidation ? setPayDeductionTabValidation(electricalSignature) : {}
    }
    setPayDeductionTabValidation ? setPayDeductionTabValidation(electricalSignature) : {}

    onPayDeductionTabData ? onPayDeductionTabData({
      [PAY_DEDUCTION_PAYMENT_ELOTRONIC_SIGNATURE]: {
        isValidated: true,
        value: electricalSignature ? "signed" : undefined,
      },
      [PAY_DEDUCTION_PAYMENT_NUMBER]: {
        isValidated: isValidPayrollNumber,
        value: Refs[PAY_DEDUCTION_PAYMENT_NUMBER].current?.value,
      },
    }) : {}
  }, [electricalSignature, isValidPayrollNumber])

  const updateValidation = (nameParam: string, wasSuccessful: boolean) => {
    switch (nameParam) {
      case PAY_DEDUCTION_PAYMENT_NUMBER:
        setIsValidPayrollNumber(wasSuccessful)
        return
    }
  }

  const inputFieldFactory = (children: React.ReactNode) => {
    return (
      <InputFieldContainer>
        {children}
      </InputFieldContainer>)
  }

  const createInputField = (
    label: string,
    placeHolderParam: string,
    nameParam: string,
    maxLength?: number,
    labelNotRequire?: string,
    aRef?: React.RefObject<HTMLInputElement>,
    type?: string,
    hideValidate?: any): React.ReactNode => {
    return (
      <TextInput
        placeholder={placeHolderParam}
        name={nameParam}
        type={type || "text"}
        maxLength={maxLength}
        {...{ label, message: getMessages, labelNotRequire }}
        onInputSucessCallback={wasAnInputSuccess => {
          updateValidation(nameParam, wasAnInputSuccess)
        }}
        ref={aRef}
        inputFromProps={checkFetchData(nameParam) ?
          getPrefetchData[nameParam]?.value : undefined}
        isValidated={getPrefetchData[nameParam]?.isValidated}
        hasValueChange={(isChange) => {
          setInputValueChange(isChange)
        }}
        hideValidate={hideValidate}
      />
    )
  }

  return (
    <Container>
      <PayrollNumberRequired isRequired={isPayrollNumberRequired}>
        {inputFieldFactory(
          createInputField("Payroll number", "Type payroll number", PAY_DEDUCTION_PAYMENT_NUMBER,
            undefined, undefined, Refs[PAY_DEDUCTION_PAYMENT_NUMBER])
        )}
      </PayrollNumberRequired>
      <Label> Payroll deduction agreement {' '}
        <LabelIcon>*</LabelIcon>
        <div
          style={{
            marginTop: `1.5rem`
          }}>
          {expandableParagraphDescriptionTextProp &&
          <ExpandableParagraph
            longtext={expandableParagraphIsLongTextProp}
          >
            {expandableParagraphDescriptionTextProp}
          </ExpandableParagraph>
          }
          {expandableParagraphPayrollDescriptionTextProp &&
          <ExpandableParagraph
            longtext={expandableParagraphIsLongTextProp}
          >
            {expandableParagraphPayrollDescriptionTextProp}
          </ExpandableParagraph>
          }
        </div>
      </Label>

      <ESignature
        terms={`By signing this agreement, you agree to the Terms and Conditions of the Payroll Deduction Request and Service Agreement`}
        onComplete={setElectricalSignature}
      />

      <SummarySection
        summaryTableDataProp={[{
          title: "Weekly debit amount:",
          value: "$20.40"
        }]}
        noteOnPaymentProp={[
          "Membership fees will be automatically deducted from your pay and will appear on your payslip",
          "If you dont work any hours that pay cycle, you wont be charged any fees",
          "Your membership can be cancelled at any time",
          "Membership fees are tax-deductible",
          "Transactions will appear on your statement as SDA UNION"
        ]}
        hasTermCheckbox={false}
      />

    </Container>
  )
}

export default PayDeductionTab
