import React from 'react'
import Truncate from 'react-truncate'
import styled from 'styled-components'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPlus, faMinus } from '@fortawesome/free-solid-svg-icons'
import CustomScroll from 'react-custom-scroll'
import { ScrollBar } from '../ScrollBarComponent'

interface StyledDesProps {
  lines: any
}
const StyledDes = styled(Truncate) <StyledDesProps>`
  text-align: left;
  font-size: 1rem;
  font-weight: 500;
  font-family: museo-sans, sans-serif;
  line-height: 1.75rem;
  letter-spacing: -0.05px;
  color: #4A4A4A;
  opacity: 1;
  line-height: 1.75rem;
  @media screen and (max-width: 768px) {
    font-size: 0.9375rem;
    line-height: 1.375rem;
  }
`
const StyledDesNoExpand = styled.div`
  text-align: left;
  font-size: 1rem;
  font-weight: 500;
  font-family: museo-sans, sans-serif;
  letter-spacing: -0.05px;
  color: #4A4A4A;
  opacity: 1;
  line-height: 1.75rem;
  @media screen and (max-width: 768px) {
    font-size: 0.9375rem;
    line-height: 1.375rem;
  }
`

const StyledIcon = styled(FontAwesomeIcon)`
  color: #FF6820;
  width: 12px !important;
  text-align: center;
  font-size: 12px;
  @media screen and (max-width: 768px) {
    width: 0.625rem !important;
  }
`
const StyledExpand = styled.a`
  text-align: right;
  text-decoration: underline;
  font-size: 1rem;
  font-weight: 500;
  font-family: museo-sans, sans-serif;
  letter-spacing: 0px;
  color: #012F57;
  opacity: 0.7;
  margin-right: 0.625rem;
  line-height: 1.875rem;
  @media screen and (max-width: 768px) {
    font-size: 0.875rem;
  }
`
const ExpandButton = styled.div`
  width: max-content;
  flex-direction: row;
  margin-top: 0.625rem;
  cursor: pointer;
  &:hover ${StyledIcon} {
    color: #00afd9;
  }
  &:hover ${StyledExpand} {
    opacity: 1;
  }
`

const StyledGradient = styled.div`
  width: 100%;
  height: 4.7rem;
  transform: matrix(-1, 0, 0, -1, 0, 0);
  background: transparent linear-gradient(180deg, #FFFFFF 0%, #FFFFFFFE 21%, #FFFFFFEF 48%, #FFFFFFA5 76%, #FFFFFF00 100%) 0% 0% no-repeat padding-box;
  opacity: 1;
  bottom: 1.5rem;
  left: 0;
  position: absolute
`
const StyledGradientEx = styled.div`
  width: 100%;
  height: 1.875rem;
  background: transparent linear-gradient(180deg, #FFFFFF00 0%, #FFFFFFA5 20%, #FFFFFFEF 48% , #FFFFFFFE 76%, #FFFFFF 100%) no-repeat;
  background-size: 97%;
  opacity: 1;
  bottom: 1.5rem;
  left: 0;
  position: absolute
`
const ContainerScroll = styled.div`
  ${ScrollBar}
`
export type Props = {
  longtext: boolean
}
export const ExpandableParagraph: React.FC<Props> = (props) => {
  const { children } = props
  const [expanded, changeExpanded] = React.useState(false)
  const [truncated, changeTruncated] = React.useState(false)
  const handleTruncate = (data: boolean) => {
    if (data !== truncated) {
      changeTruncated(truncated)
    }
  }
  if (props.longtext) {
    return (
      <div style={{ position: 'relative' }}>
        {!expanded && (
          <StyledGradient />
        )}
        {expanded && (
          <StyledGradientEx />
        )}
        <StyledDes
          lines={!expanded && 5}
          ellipsis={(
            <ExpandButton
              onClick={() => { changeExpanded(true) }}
            >
              <StyledExpand>
                Expand
              </StyledExpand>
              <StyledIcon icon={faPlus} />
            </ExpandButton>
          )}
          onTruncate={handleTruncate}
        >
          <div style={{ display: 'flex', height: '11.25rem', width: '100%' }}>
            <ContainerScroll style={{ flex: 1, minWidth: 0, minHeight: 0, overflowY: 'scroll', overflowX: 'hidden' }}>
              <CustomScroll
                allowOuterScroll={true}
              >
                <div style={{ marginRight: '1.25rem' }}>
                  {children}
                </div>
              </CustomScroll>
            </ContainerScroll>
          </div>
        </StyledDes>
        {!truncated && expanded && (
          <ExpandButton
            onClick={() => { changeExpanded(false) }}
          >
            <StyledExpand>
              Minimise
            </StyledExpand>
            <StyledIcon icon={faMinus} />
          </ExpandButton>
        )}
      </div>
    )
  } else {
    return (
      <div style={{ position: 'relative' }}>
        <StyledDesNoExpand
        >
          {children}
        </StyledDesNoExpand>
      </div>
    )
  }
}

export default ExpandableParagraph
