// PLEASE REFACTOR THIS
export const handlePhoneNumber = (inputGetter: string, event: any) => {
  let phoneNumberStartWith02030708: any = renderPhoneNumberStrat1(inputGetter)
  let phoneNumberStartWith04: any = renderPhoneNumberStrat2(inputGetter)
  let result = event.target.value
  if (inputGetter.length === 3
    && ['02', '03', '07', '08'].includes(inputGetter.substring(0, 2))
    && inputGetter[2] !== ' ') {
    result = phoneNumberStartWith02030708
  }
  else if (inputGetter.length === 8
    && ['02', '03', '07', '08'].includes(inputGetter.substring(0, 2))
    && inputGetter[7] !== ' ') {
    result = phoneNumberStartWith02030708
  }
  else if (inputGetter.length === 5 && inputGetter.substring(0, 2) === '04'
    && inputGetter[4] !== ' ') {
    result = phoneNumberStartWith04
  }
  else if (inputGetter.length === 9 && inputGetter.substring(0, 2) === '04'
    && inputGetter[8] !== ' ') {
    result = phoneNumberStartWith04
  }
  if ((inputGetter.length === 10 || inputGetter.length === 11)
    && ['02', '03', '07', '08'].includes(inputGetter.substring(0, 2))) {
    result = phoneNumberStartWith02030708
  }
  else if ((inputGetter.length === 10 || inputGetter.length === 11)
    && inputGetter.substring(0, 2) === '04') {
    result = phoneNumberStartWith04
  }
  return result
}

export const handleDateOnDesktop = (inputGetter: string, event: any) => {
  let result = renderDate(inputGetter)

  if (inputGetter.length === 3 && inputGetter[2] !== ' ') {
    if (inputGetter[1] === '/') {
      result = '0' + result?.slice(0, 1) + " " + result?.slice(1);
      event.target.value = result
    } else {
      event.target.value = result
    }
  }
  if (inputGetter.length === 4 && inputGetter.slice(-1) !== '/') {
    event.target.value = result
  }
  if (inputGetter.length === 5 && inputGetter[4] !== ' ') {
    event.target.value = result
  }
  if (inputGetter.length === 8 && inputGetter[7] !== ' ') {
    if (inputGetter[6] === '/') {
      result = result?.slice(0, 5) + '0' + result?.slice(5, 6) + " " + result?.slice(6);
      event.target.value = result
    } else {
      event.target.value = result
    }
  }
  if (inputGetter.length === 9 && inputGetter.slice(-1) !== '/') {
    event.target.value = result
  }
  if (inputGetter.length === 10 && inputGetter[9] !== ' ') {
    event.target.value = result
  }
  return event.target.value
}

